.slideCaption,
.slideList {
  right: 0;
  position: absolute;
  height: 394px;
}
.mainContainer {
  position: absolute;
  height: 394px;
}
.slideContainer {
  overflow: hidden;
}
.slideList {
  width: 223px;
  outline: 0;
  border: 0;
  height: 394px;
  justify-content: center;
  align-items: center;
  line-height: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  -webkit-transition: opacity 2s ease-in-out;
  -moz-transition: opacity 2s ease-in-out;
  -o-transition: opacity 2s ease-in-out;
  transition: opacity 2s ease-in-out;
}
.slideCaption {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.8em;
  top: 45%;
  left: 0;
}
.buttonWrapperContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
}
.nextButton {
  margin-right: 20px;
}
.prevButton {
  margin-left: 20px;
}
.nextButton,
.prevButton {
  background-size: 100%;
  background-color: rgba(241, 226, 226, 0.2);
  height: 30px;
  width: 30px;
  border-radius: 4px;
  color: #fff;
  border: none;
  outline: 0;
  cursor: pointer;
  opacity: 0.5;
  transition: 0.8s;
}
.nextButton:hover,
.prevButton:hover {
  opacity: 1;
}
.indicatorContainer {
  position: relative;
}
.indicatorWrapperUl {
  z-index: 2;
  padding: 0;
  margin: 0;
  position: absolute;
  top: -40px;
  left: 50%;
}
.indicate {
  display: inline-block;
  margin-right: 5px;
  width: 12px;
  height: 12px;
  font-size: 16px;
  line-height: 30px;
  border-radius: 100%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.8);
  color: #333;
  text-indent: -9999px;
  cursor: pointer;
  transition: all 0.2s linear;
}
div.round button {
  border-radius: 50%;
  height: 35px;
  width: 35px;
}
